import React from 'react'
import {useState,useEffect} from 'react'
import ReactTable from 'react-table'
import 'react-table/react-table.css'
import Cookies from 'js-cookie'
import Skeleton from '../skeleton'
import Web3 from 'web3'
import defaultVars from '../../libs/config'
import BigNumber from 'bignumber.js'
import contractAbi from '../../libs/abi'
import {FormattedMessage} from 'gatsby-plugin-intl'
import {BounceLoader} from 'react-spinners'
import { toast } from 'react-toastify'
const web4 = new Web3(defaultVars.provider);
const contract = new web4.eth.Contract(contractAbi,defaultVars.contractAddress);
// const web5 = new Web3(defaultVars.provider);
// const contract5 = new web5.eth.Contract(contractAbi,defaultVars.contractAddress);
const regAmount = /^\d+(\.\d{0,8})?$/;
const columns = [{
    Header: props => <FormattedMessage id="db.state" defaultMessage="State"/>,
    accessor: 's', // String-based value accessors!,
    Cell: props => props.value === 'dE' ? <span className="text-lights-bluebold">Deposit</span>:<span className="text-lights-orangebold">Withdrawal</span>
    }, {
    Header: props => <FormattedMessage id="stats.amount" defaultMessage="Amount"/>,
    accessor: 'v',
    Cell: props => <span className='number'>{props.value}</span> // Custom cell components!
    },{
    Header: props => <FormattedMessage id="stats.date" defaultMessage="Date"/>,
    accessor: 'date',
    Cell: props => {
        const t = new Date(Number(props.value*1000))
        return `${t.getFullYear()}-${("0" + (t.getMonth()+1)).slice(-2)}-${("0" + t.getDate()).slice(-2)} ${("0"+t.getHours()).slice(-2)}:${("0" + t.getMinutes()).slice(-2)}`;
    }
}]
const sendCols = [{
    Header: props => <FormattedMessage id="db.state" defaultMessage="State"/>,
    accessor: 's', // String-based value accessors!,
    Cell: props => props.value === 'bRE' ? <span className="text-lights-bluebold">Success</span>:<span className="text-lights-orangebold">Fail</span>
    }, {
    Header: props => <FormattedMessage id="stats.level" defaultMessage="Level"/>,
    accessor: 'l',
    Cell: props => <span className='number'>{props.value}</span> // Custom cell components!
    },{
    Header: props => <FormattedMessage id="stats.to" defaultMessage="To"/>,
    accessor: 'u',
    Cell: props => <span className='number'>{props.value}</span> // Custom cell components!
    },{
    Header: props => <FormattedMessage id="stats.date" defaultMessage="Date"/>,
    accessor: 'date',
    Cell: props => {
        const t = new Date(Number(props.value*1000))
        return `${t.getFullYear()}-${("0" + (t.getMonth()+1)).slice(-2)}-${("0" + t.getDate()).slice(-2)} ${("0"+t.getHours()).slice(-2)}:${("0" + t.getMinutes()).slice(-2)}`;
    }
}]
const AdminDashboard = () => {
    const [activeTab,setActiveTab] = useState(0);
    const [balance,setBalance] = useState(null);
    const [userInputAmount,setuserInputAmount] = useState('');
    const [send,setSend] = useState(false);
    const [userLevel,setUserLevel] = useState('level');
    const [userInputAddress,setUserInputAddress] = useState('');
    const [pay,setPay] = useState(null);
    const [sendList,setSendList] = useState(null)
    useEffect(()=>{
        const a = async()=>{
            const bl = await web4.eth.getBalance(defaultVars.contractAddress);
            setBalance(web4.utils.fromWei(bl,"ether"));
            let payListMin = [];
            const payList = await contract.getPastEvents("allEvents",{fromBlock: 0,topics:[["0x63ef0d3bb62bb6d3239f0fc3e088e2f188f4274be2445312920fb2a9dcb101a3","0x4bd94957f264cc1eb10349f3d2e6d04f1fbf35168497b24b2262150ac5193672"]]})
            console.log(payList)
            for (let i of payList){
                payListMin.push({v: web4.utils.fromWei(i.returnValues._v.toString(),"ether"),date: i.returnValues._t.toString(),s: i.event})
            }
            setPay(payListMin.reverse())
            let sendListMin = [];
            const sendList = await contract.getPastEvents("allEvents",{fromBlock: 0,topics:[["0x33140448085742ee74af13ac81a628068c485c2ea74d3b368ed0e0fca059fe93","0x154bd3f0184bafa2f1300062924f0db714a9bf2cc5ff17ee8fbf5bfa9471ee16"]]})
            console.log(sendList);
            for (let i of sendList){
                sendListMin.push({u: i.returnValues._u.toString(),l:i.returnValues._l.toString(),s: i.event,date: i.returnValues._t.toString()})
            }
            setSendList(sendListMin.reverse())
        }
        a();
    },[null])
    const handleUserInputAmount = (e) => {
        if (regAmount.test(e.target.value) || e.target.value === ''){
            setuserInputAmount(e.target.value)
        }
    }
    const handleDeposit = async() => {
        try {
            if (!send){
                setSend(true)
                window.web3 = new Web3(window.ethereum,null,{transactionConfirmationBlocks: 1});
                if (!regAmount.test(userInputAmount)){
                    toast.error('Please input amount')
                    return setSend(false)
                }
                const acc = await window.ethereum.enable();
                const gasPrice = await window.web3.eth.getGasPrice();
                const bigNumberGwei = new BigNumber(gasPrice).plus(defaultVars.gweiPlus).toString();
                let gasCal = await window.web3.eth.estimateGas({from: acc[0],to: defaultVars.contractAddress,value: web4.utils.toWei(userInputAmount,'ether'),data: contract.methods.funcD().encodeABI()});
                await window.web3.eth.sendTransaction({from: acc[0],to: defaultVars.contractAddress,value: web4.utils.toWei(userInputAmount,"ether"),data: contract.methods.funcD().encodeABI(),gas: gasCal,gasPrice: bigNumberGwei});
                toast.success('Deposit success.')
                setSend(false);
            }
        } catch (err){
            console.log(err)
        }
        
    }
    const handleWithdrawal = async() => {
        try {
            if (!send){
                setSend(true);
                window.web3 = new Web3(window.ethereum,null,{transactionConfirmationBlocks: 1});
                const acc = await window.ethereum.enable();
                const gasPrice = await window.web3.eth.getGasPrice();
                const bigNumberGwei = new BigNumber(gasPrice).plus(defaultVars.gweiPlus).toString();
                const bl = await web4.eth.getBalance(defaultVars.contractAddress);
                let gasCal = await window.web3.eth.estimateGas({from: acc[0],to: defaultVars.contractAddress,value: '0x',data: contract.methods.funcE(bl).encodeABI()});
                await window.web3.eth.sendTransaction({from: acc[0],to: defaultVars.contractAddress,value: '0x0',data: contract.methods.funcE(bl).encodeABI(),gas: gasCal,gasPrice: bigNumberGwei});
                toast.success('Withdrawal success.')
                setSend(false);
            }
        } catch(err){
            setSend(false);
        }
    }
    const handleSetLevel = async() => {
        try {
            if (!send){
                setSend(true);
                if (!web4.utils.isAddress(userInputAddress)){
                    toast.error('Address invalid.')
                    return setSend(false);
                }
                if (userLevel === 'level'){
                    toast.error('Please select level.')
                    return setSend(false);
                }
                window.web3 = new Web3(window.ethereum,null,{transactionConfirmationBlocks: 1});
                const acc = await window.ethereum.enable();
                const gasPrice = await window.web3.eth.getGasPrice();
                const bigNumberGwei = new BigNumber(gasPrice).plus(defaultVars.gweiPlus).toString();
                let gasCal = await window.web3.eth.estimateGas({from: acc[0],to: defaultVars.contractAddress,value: '0x',data: contract.methods.funcF(userInputAddress,userLevel).encodeABI()});
                console.log(gasCal)
                await window.web3.eth.sendTransaction({from: acc[0],to: defaultVars.contractAddress,value: '0x0',data: contract.methods.funcF(userInputAddress,userLevel).encodeABI(),gas: gasCal,gasPrice: bigNumberGwei});
                toast.success('Set user level success.')
                setSend(false);
            }
        } catch (err){
            setSend(false);
        }
    }
    return (
        <div className="overflow-hidden">
            <div className="md:flex md:-mx-2 mb-4">
                <div className="w-full md:w-1/2 md:px-2">
                    {balance !== null ? <div className="px-2 py-2 bg-white shadow">
                        <div className="flex justify-between mb-4">
                            <div className="text-16 font-sans font-bold text-lights-bluebold">Balances</div>
                            <div className="">{balance} ETH</div>
                        </div>
                        <div className="flex justify-between">
                            <button style={{width: '120px'}} className="inline-flex items-center justify-center px-2 py-2 bg-lights-orangebold text-white rounded" onClick={handleWithdrawal}>{send ? <BounceLoader size={24} color="#fff"/>: 'Withdrawal all'}</button>
                            <div className="inline-flex items-center">
                                <input type="" className="w-32 px-2 py-2 border mr-2 rounded focus:border-lights-bluebold focus:outline-none" value={userInputAmount} onChange={handleUserInputAmount}/>
                                <button style={{width: '70px'}} className="px-2 py-2 bg-lights-bluebold text-white rounded inline-flex items-center justify-center" onClick={handleDeposit}>{send ? <BounceLoader color="#fff" size={24} /> : 'Deposit'}</button>
                            </div>
                        </div>
                    </div> :<div className="px-2 py-2 bg-white shadow">
                        <div className="flex justify-between mb-4">
                            <div className="text-16 font-sans font-bold text-lights-bluebold"><Skeleton width={120}/></div>
                            <div className=""><Skeleton width={70}/></div>
                        </div>
                        <div className="flex justify-between">
                            <Skeleton width={120} height={42}/>
                            <Skeleton width={205} height={42} />
                        </div>
                    </div> }
                </div>
                <div className="w-full md:w-1/2 md:px-2">
                    {balance !== null ? <div className="p-2 bg-white shadow">
                        <div className="text-16 font-sans font-bold text-lights-bluebold mb-4">Set Level User</div>
                        <div className="flex justify-between">
                            <input type="text" className="px-1 py-2 border rounded flex-1 mr-2 focus:outline-none focus:border-lights-bluebold" value={userInputAddress} onChange={(e)=>setUserInputAddress(e.target.value)}/>
                            <select value={userLevel} className="appearance-none px-4 py-2 bg-lights-orangebold rounded text-white mr-1" onChange={(e)=>setUserLevel(e.target.value)}>
                                <option value="level">level ?</option>
                                <option value={2}>2</option>
                                <option value={3}>3</option>
                                <option value={4}>4</option>
                                <option value={5}>5</option>
                            </select>
                            <button onClick={handleSetLevel} style={{width: '82px'}} className="inline-flex items-center justify-center px-2 py-2 border rounded focus:outline-none bg-lights-bluebold text-white">{send ? <BounceLoader size={24} color="#fff"/> : 'Set Level'}</button>
                        </div>
                    </div>:<div className="p-2 bg-white shadow">
                        <div className="text-16 font-sans font-bold text-lights-bluebold mb-4"><Skeleton width={120}/></div>
                        <Skeleton height={42}/>
                    </div>}
                </div>
            </div>
            {/* block 2 */}
            {balance ? <div className="bg-white shadow p-2">
                <div className="text-center text-16 font-sans font-bold text-lights-orangebold uppercase mb-4">History</div>
                <div className="text-center mb-4">
                    <button onClick={()=>setActiveTab(0)} className={`block w-full mb-2 md:mb-0 md:mr-2 border rounded md:inline md:w-auto px-2 py-2 ${activeTab === 0 ? 'bg-lights-bluebold text-lights-orangebold border-lights-bluebold' :'text-light-bluebold bg-white'}`}>Withdrawal, Deposit</button>
                    <button onClick={()=>setActiveTab(1)} className={`block w-full mb-2 md:mb-0 border rounded md:inline md:w-auto px-2 py-2 ${activeTab === 1 ? 'bg-lights-bluebold text-lights-orangebold border-lights-bluebold' :'text-light-bluebold bg-white'}`}>Bonus send</button>
                </div>
                {
                    activeTab === 0 ? <div className="">
                    {pay !== null ? <ReactTable
                        data={pay}
                        columns={columns}
                        defaultPageSize={10}
                    /> : <Skeleton count={18}/>}
                </div>:null
                }
                {
                    activeTab===1 ?  <div className="">
                    {sendList !== null ? <ReactTable data={sendList} columns={sendCols} defaultPageSize={10}/>:<Skeleton count={18}/>}
                </div>:null
                }               
            </div>:<div className="bg-white shadow p-2">
                <div className="text-center text-16 font-sans font-bold text-lights-orangebold uppercase mb-4"><Skeleton width={80} height={24}/></div>
                <div className="text-center mb-4 md:flex md:justify-center">
                    <div className="md:w-48 md:mr-2">
                        <Skeleton height={42}/>
                    </div>
                    <div className="md:w-32"><Skeleton height={42}/></div>
                </div>
                <div className="">
                    <Skeleton count={40}/>
                </div>
            </div>}
        </div>
    )
}
export default AdminDashboard