import React from 'react'
import {useState,useEffect} from 'react'
import Nav from './nav'
import Footer from './footer'
import Cookies from 'js-cookie'
import {navigate} from 'gatsby'
import defaultVars from '../libs/config'
import {BarLoader} from 'react-spinners'
import logo from '../images/logo-loading.svg'
import {DashboardIcon,BonusIcon,LogoutIcon,UplineIcon,StatIcon,ReferralIcon,MediaIcon,AdminIcon} from './common'
import {injectIntl,Link} from 'gatsby-plugin-intl'
import { ToastContainer,toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import SEO from './seo'

const dbNav = [
    {path: '/db/dashboard/',icon: 'db',name: 'Dashboard',id: 'db.nav.db'},
    {path: '/db/media/',icon: 'media',name: 'Media',id: 'db.nav.media'},
    {path: '/db/stats/',icon: 'stats',name: 'Stats',id: 'db.nav.stats'},
    {path: '/db/referrals/',icon: 'ref',name: 'Referrals',id: 'db.nav.ref'},
    {path: '/db/uplines/',icon: 'upline',name: 'Uplines',id: 'db.nav.up'},
    {path: '/db/admin/',icon: 'admin',name: 'Admin',id: 'db.admin'},
    // {path: '/db/bonus/',icon: 'bonus',name: 'Commission statistics',id: 'db.nav.bonus'},
    {path: '/db/logout/',icon: 'logout',name: 'Logout',id: 'db.nav.logout'},
]

const Icons = (name) => {
    switch(name){
        case 'db':
            return <DashboardIcon />;
        case 'bonus': 
            return <BonusIcon />;
        case 'logout': 
            return <LogoutIcon />;
        case 'upline':
            return <UplineIcon />;
        case 'stats':
            return <StatIcon />;
        case 'ref':
            return <ReferralIcon />;
        case 'media':
            return <MediaIcon />;
        case 'admin':
            return <AdminIcon />
    }
}
const DbLayout = ({intl,children}) => {
    const [loading,setLoading] = useState(true);
    const [userAdmin,setUseraSAdmin] = useState(false);
    useEffect(()=>{
        const user = Cookies.get('user');
        const a = async() => {
            if (user){
                if(window && window.ethereum){
                    try {
                        const acc = await window.ethereum.enable();
                        console.log(acc);
                        if (acc && acc[0] && acc[0].toLowerCase() === defaultVars.owner.toLowerCase()){
                            setUseraSAdmin(user); 
                            return setLoading(false);
                        } else {
                            toast.error('Please log in with your contract owner address.')
                            toast.info('Navigate to logout after 3 seconds.')
                            setTimeout(()=>{
                                navigate('/db/logout/');
                            },3000)
                        }
                    } catch (err){
                        toast.error('Error - unknown.')
                        toast.info('Navigate to logout after 3 seconds.')
                        setTimeout(()=>{
                            navigate('/db/logout/');
                        },3000)
                    }
                } else {
                    toast.error('Can not detect your provider.');
                    toast.info('Navigate to logout after 3 seconds.');
                    setTimeout(()=>{
                        navigate('/db/logout/');
                    },3000)
                }
            } else {
                navigate('/login/')
            }
        }
        a();
    },[null])
    if (loading){
        return (
            <div className="h-screen flex items-center justify-center">
                <ToastContainer />
                <div className="text-center">
                    <img src={logo} alt="" className="mb-2"/>                    
                    <div className="flex items-center justify-center">
                        <BarLoader width={200} height={4} color={'#24405c'}/>
                    </div>
                </div>
            </div>
        )
    }
    return (
        <>
            <Nav />
            <ToastContainer />
            <SEO title="Dashboard" />
            <main style={{backgroundColor: '#f7f6f6'}}>
                <div className="container mx-auto px-2 lg:px-4 py-4 md:flex">
                    <div className="db-nav flex">
                        {dbNav.map((val)=>{
                            if (val.name === 'Admin'){
                                if (userAdmin.toLowerCase() === defaultVars.owner.toLowerCase()){
                                    return (
                                        <Link to={val.path} className="flex-1 flex items-center justify-center text-lights-bluebold font-sans font-semibold text-11 lg:text-14 lg:justify-start lg:py-2" activeClassName="text-lights-orangebold">
                                                <span className="md:mr-2 lg:mr-4">
                                                    {Icons(val.icon)}
                                                </span>
                                                <span className="hidden md:inline">{intl.formatMessage({id: val.id,defaultMessage: val.name})}</span>
                                        </Link>
                                    )
                                }
                            } else {
                                return (
                                    <Link to={val.path} className="flex-1 flex items-center justify-center text-lights-bluebold font-sans font-semibold text-11 lg:text-14 lg:justify-start lg:py-2" activeClassName="text-lights-orangebold">
                                            <span className="md:mr-2 lg:mr-4">
                                                {Icons(val.icon)}
                                            </span>
                                            <span className="hidden md:inline">{intl.formatMessage({id: val.id,defaultMessage: val.name})}</span>
                                    </Link>
                                )
                            }
                        })}
                    </div>
                    <div className="flex-1">{children}</div>
                </div>
            </main>
            <Footer />
        </>
    )
}

export default injectIntl(DbLayout)